<template>
  <div>
    <table class="table table-boardered">
      <tr>
        <td colspan="2">질문 내용</td>
      </tr>
      <tr>
        <td colspan="2">
          <textarea
            id="question"
            cols="46"
            rows="10"
            ref="question"
            v-model="qna.question"
          ></textarea>
        </td>
      </tr>
      <tr>
        <td colspan="2">
          <div class="text-center">
            <button class="btn btn-primary" @click="createHandler">등록</button>
            <button class="btn btn-primary" @click="moveHandler">목록</button>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import http from "@/api/http";
export default {
  data() {
    return {
      qna: {
        userid:"dd@d.com",
        question: "",
      },
    };
  },
  methods: {
    moveHandler() {
      this.$router.push({ name: "QnAListView" });
    },
    createHandler() {
      //데이타 검증
      let err = false;
      let msg = "";

      if (this.qna.question == "") {
        err = true;
        msg = "내용을 입력해 주세요";
        this.$refs.question.focus();
      }

      if (err) {
        alert(msg);
      } else {
        //등록 처리
        //localStorage에 이미 저장된 책 목록이 있는지 검사
        http.post("/qna", this.qna).then(({data})=>{
          if(data === "success") msg = "등록 성공!!!";
          alert("등록 완료");
          this.moveHandler();
        });

      }
    },
  },
};
</script>

<style></style>
