<template>
  <div>
    <div class="row">
      <div class="col-2"></div>

      <div class="col-2">
          <b-form-select v-model='key' :options='keys'></b-form-select>
      </div>
      <div class="col-4">
        <b-form-input v-model="word" />
      </div>
      <div class="col-2">
        <b-button variant="primary"  @click='searchQna'>검색</b-button>
      </div>
      <div class="col-2"></div>
    </div>
    <div v-if="qnas.length > 0">
      <b-table hover striped :items="qnas" :fields="fields" @row-clicked="goDetail"></b-table>
    </div>
    <div v-else>등록된 질문 목록이 없습니다.</div>
  </div>
</template>

<script>
import http from "@/api/http"

export default {
  data() {
    return { 
      qnas: [],
      word:"",
      keys:[
        {value:"all", text:'--선택하세요--'},
        {value:"no", text:'질문 번호'},
      ],            //select option에 표시할 데이터 
      fields:[
        {key:"no", label:"질문 번호"},{key:"userid", label:"사용자"},
      ],              //table의 header에 표시되는 데이터 
      key:"all",
    };
  },
  created() {
    this.searchQna();
  },
  methods:{
    searchQna(){
      console.log('key.......', this.key)
      const url =`qna?key=${this.key}&pageNo=1&word=${this.word}`;
      http.get(url)
          .then(({data})=>{
            console.log('응답 데이타', data)
            this.qnas = data;
          })
    },
    goDetail(item){   //table에서 클릭된 row의 데이터가 인자로 전달됨
      this.$router.push({name:"QnaDetailView", query:{no:item.no}})
    },
  },
};
</script>

